<template>
  <div>
    <v-header>
      <h1 slot="title">就餐详情</h1>
    </v-header>
    <section class="section">
      <ul class="section-list">
        <li>
          <h1><img src="../assets/icon/点心.png" alt="">早餐</h1>
          <div class="section-context">
            <div class="section-list-left">
              <div class="time">
                <span class="time-num"> 小米粥</span>
              </div>
              <p class="start">蒸煮，想起浓郁，口感软绵，熬粥，香糯顺滑</p>
            </div>
            <div class="section-list-right">
              <img src="//img30.360buyimg.com/sku/jfs/t1/116860/12/2542/134304/5ea25b2eEf09eaa22/375c1dbd8109bfca.jpg">
            </div>
          </div>

        </li>
        <li>
          <h1><img src="../assets/icon/饭碗.png" alt="">午餐</h1>
          <div class="section-context">
            <div class="section-list-left">
              <div class="time">
                <span class="time-num">米饭</span>
              </div>
              <p class="start">选取优质大米原料，大可丰满的肉粒配着丰富营养的蔬菜</p>
            </div>
            <div class="section-list-right">
              <img src="//img30.360buyimg.com/sku/jfs/t1/59016/19/9743/229932/5d70c324E34196de7/e45d3d6fed9141bf.jpg">
            </div>
          </div>

        </li>
        <li>
          <h1><img src="../assets/icon/点心.png" alt="">晚餐</h1>
          <div class="section-context">
            <div class="section-list-left">
              <div class="time">
                <span class="time-num">营养麦片</span>
              </div>
              <p class="start">优质膳食纤维，保留新鲜水果的维生素营养</p>
            </div>
            <div class="section-list-right">
              <img src="//img12.360buyimg.com/n1/jfs/t26008/85/2282507280/130002/40a54972/5bc6d97fNc685da59.jpg">
            </div>
          </div>

        </li>
        <li>
          <h1><img src="../assets/icon/素食者.png" alt="">水果</h1>
          <div class="section-context">
            <div class="section-list-left">
              <div class="time">
                <span class="time-num">水果沙拉</span>
              </div>
              <p class="start">有机蔬菜</p>
            </div>
            <div class="section-list-right">
              <img src="http://img30.360buyimg.com/popWaterMark/jfs/t13600/80/2340786827/280193/bb38b086/5a3a6bd4N59c8eed5.jpg">
            </div>
          </div>

        </li>
      </ul>
      <v-footer></v-footer>
    </section>
    <base-line></base-line>
  </div>
</template>

<script>
  import HeaderVue from '../common/_header.vue'
  import Footer from '../common/_footer'
  import BaseLine from '../common/_baseline'
  export default {
    components: {
      "v-header": HeaderVue,
      "v-footer": Footer,
      "base-line":BaseLine
    }
  }

</script>

<style lang="less" scoped>
  @import "../assets/fz.less";
  @import "../assets/index/style.css";

  .section {
    width: 100%;
    .pt();

    .section-list {
      width: 100%;
      .bt();

      >li {
        display: -ms-flex;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        padding: 4vw 5vw 2vw 12vw;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        flex-direction: column;
        width: 100%;

        .section-context {
          display: flex;
          flex-direction: row;
          padding-top:2vh;
        }

        h1 {
          .fz(font-size, 30);
          line-height: 4.0vw;
          letter-spacing: 0.42vw;
          text-align: center;
        }

        .section-list-left {
          width: 50%;


          .time {
            .time-num {
              display: inline-block;
              text-align: center;
              padding: 1.6vw;
              color: #fff;
              border-radius: 0.5vw;
              background-color: #999999;
              .fz(font-size, 26);
              letter-spacing: 0.3vw;
            }

            .time-col {
              color: #333;
              width: 2vw;
              display: inline-block;
              text-align: center;
              font-weight: 700;
              .fz(font-size, 30);
            }
          }

          .start {
            .fz(font-size, 30);
            padding-top: 4vw;
            letter-spacing: 0.3vw;
          }
        }

        .section-list-right {
          width: 50%;
          display: block;
          position: relative;

          img {
            display: block;
            width: 100%;
            background-color: gold;
          }

          span {
            padding: 0.3vw 1.2vw;
            border-radius: 1vw;
            text-align: center;
            position: absolute;
            bottom: 0.4vw;
            right: 0.2vw;
            background-color: @cl;
            color: #fff;
            .fz(font-size, 24);
          }
        }
      }
    }

    .section-banner {
      display: block;
      width: 100%;
      .bd();

      img {
        display: block;
        width: 100%;
      }
    }
  }

</style>



